import { computed } from 'vue';
import KroIcon from '../Icon/Icon.vue';
import KroSpinner from '../Spinner/Spinner.vue';
export default {
  components: {
    KroIcon: KroIcon,
    KroSpinner: KroSpinner
  },
  props: {
    /**
     * Adds loading icon to the button
     */
    loading: {
      type: Boolean,
      default: false
    },

    /**
     * Turns the button into a link
     */
    href: String,

    /**
     * If the button is a link, then this will set the target
     */
    target: {
      type: String,
      default: '_blank'
    },

    /**
     * Makes the button a router-link
     */
    to: String,

    /**
     * The button type
     */
    type: String,

    /**
     * Apply primary button styling to the button
     */
    primary: Boolean,
    outline: Boolean,
    raised: Boolean
  },
  setup: function setup(props) {
    var componentType = computed(function () {
      var to = props.to,
          href = props.href;
      if (to) return 'router-link';
      if (href) return 'a';
      return 'button';
    });
    return {
      componentType: componentType
    };
  }
};